<template>
  <nav>
    <v-toolbar light app >
      <v-toolbar-side-icon class="green--text" @click="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title class="green--text">
        <span class="font-weight-light">Aquacare</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <a v-if="logged">
        <v-btn flat color="green" v-on:click="log">        
          <span>{{ status_logged }}</span>
          <v-icon right>exit_to_app</v-icon>
        </v-btn>
      </a>
    </v-toolbar>
    
        <a v-if="logged">
      <v-navigation-drawer app v-model="drawer" 
      class="green lighten-5">

          <v-list>
              <v-list-tile
                v-for="item in items"
                :key="item.title"
                router :to="item.routingTo">
                <div>
                  <v-list-tile-content>
                    <v-list-tile-title class="green--text">
                      <v-icon color="black">{{ item.icon }}</v-icon> {{ item.title }}
                    </v-list-tile-title>
                  </v-list-tile-content>
                </div>
              </v-list-tile>
            </v-list>
        </v-navigation-drawer>
          </a>
          <a v-else><v-navigation-drawer disable/></a>
  </nav>
</template>


<script>
import firebase from 'firebase';
export default {

  name: 'NavBar',
  data: () => ({
    drawer: null,
    logged: firebase.auth().currentUser != null,
    items: [
      { title: 'Home', icon: 'dashboard', routingTo: '/home', action: '' },
      { title: 'Account', icon: 'account_box', routingTo: '/account', action: '' },
      // { title: 'My Aquarium', icon: 'bubble_chart', routingTo: '/aquariumSettings', action: '' },
      // { title: 'Find Desease', icon: 'find_in_page', routingTo: '/categorizer', action: '' },
      // { title: 'Scheduler', icon: 'schedule', routingTo: '/scheduler', action: '' },
      { title: 'Settings', icon: 'settings', routingTo: '/settings', action: '' },
      { title: 'About', icon: 'gavel', routingTo: '/aboutAuth', action: '' },
    ],
    color: 'primary',
    colors: [
      'primary',
        'blue',
        'success',
        'red',
        'teal',
    ],
    right: false,
    miniVariant: false,
    expandOnHover: true,
    background: true,
    status_logged: 'Log Out',
  }),
  methods: {
    log: function(){
      if  (firebase.auth().currentUser != null){
        
        firebase.auth().signOut();
        this.$root.$emit('updateVisibility', false);
        this.logged = false;
      } else {
        this.$root.$emit('updateVisibility', true);
      }
    },    
  },
  mounted() {
    this.$root.$on('updateVisibility', (value) => {
      this.logged = firebase.auth().currentUser != null;
      if  (value){
        console.log('user has logged in');
        this.$router.push('home');
        this.status_logged = 'Log Out'
      } else {
        console.log('user has logged out');

        this.$root.$emit('go2Login');
        // this.$router.push('login');
        this.status_logged = 'Log In'
      }
    })
  },
  computed: {
    bg () {
      return this.background ? 'https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg' : undefined
    },
  },
}
</script>

